<template>
  <div class="row f-0_9 pd-20">

    <!--登录 注册-->
    <div class="row t-c" v-if="!rootState.isLogin">
      <router-link to="/manage" class="g-btn g-btn-radius g-btn-primary">管理员登录</router-link>
      <router-link to="/common/login" class="g-btn g-btn-radius g-btn-primary">登录</router-link>
      <router-link to="/common/register" class="g-btn g-btn-radius g-btn-danger">注册</router-link>
    </div>

    <div class="row f-0_8 t-red t-c pd-tb-3">
      (只限于该楼层的用户使用)
    </div>

    <div class="g-panel g-panel-primary mg-t-10 border-radius">
      <div class="g-panel-title t-white">
        公告
      </div>

      <div class="row t-c pd-15">
        <template>
          公告信息
        </template>
        <template>
          没有新公告信息
        </template>
      </div>
    </div>
  </div>
</template>

<script>

  import axios from 'axios';

  import api from '@Common/api';

  const storeName = 'home/routes/';

  export default {
    data() {
      return {
        state: this.$store.state.home.index,
        rootState: this.$store.state
      }
    },
    created() {
      axios.get(api.getApi('')).then((res)=>{
        console.log(res);
      });
    }
  }
</script>



